.RichContent blockquote {
  margin: 0.2em 0 0.2em 2em;
  font-style: italic;
}
.RichContent pre {
  overflow: auto;
}
.RichContent code,
.RichContent samp,
.RichContent kbd,
.RichContent var,
.RichContent pre {
  font-family: var(--postCodeFont, monospace);
}
.RichContent p {
  margin: 0 0 1em 0;
}
.RichContent p:last-child {
  margin: 0 0 0 0;
}
.RichContent h1 {
  font-size: 1.1em;
  line-height: 1.2em;
  margin: 1.4em 0;
}
.RichContent h2 {
  font-size: 1.1em;
  margin: 1em 0;
}
.RichContent h3 {
  font-size: 1em;
  margin: 1.2em 0;
}
.RichContent h4 {
  margin: 1.1em 0;
}
.RichContent .img {
  display: inline-block;
}
.RichContent .emoji {
  display: inline-block;
  width: var(--emoji-size, 32px);
  height: var(--emoji-size, 32px);
}
.RichContent .img,
.RichContent video {
  max-width: 100%;
  max-height: 400px;
  vertical-align: middle;
  -o-object-fit: contain;
     object-fit: contain;
}